import _tool from '@/util/tools.js';
export default {
	data() {
		return {
			DataList: [], delArr: [],
		}
	},
	onLoad() {
		this.GetBookList();
	},
	methods: {
		GetBookList() {
			let that = this;
			_tool.GetBookListData().then(function(res) {
				if(res == "" || res == null || res == undefined) {
					console.log(res);
					that.DataList = [];
				} else {
					that.DataList = res;
				}
			});
		},
		BackClick() {
			uni.navigateBack()
		},
		CheckboxChange (e) {
			let DataList = this.DataList, values = e.detail.value;
			this.delArr = values;
			for (let i = 0; i < DataList.length; ++i) {
				const item = DataList[i];
				if(values.includes(DataList.id)){
					this.$set(item,'checked',true)
				}else{
					this.$set(item,'checked',false)
				}
			}
		},
		DelClick() {
			let that = this;
			_tool.DelBookList(this.delArr).then(function(res) {
				that.GetBookList();
			});
		},
		clearClick() {
			let that = this;
			_tool.clearStorage().then(function(res) {
				that.GetBookList();
			});
		}
	}
}
